import React from "react";
import Thermometer from "react-thermometer-component";
import '../thermometer.css'

class ThermometerRender extends React.Component {

  constructor(props){
    super(props);
    this.state = {temperature: "10"}
  }

  componentDidMount(){
    this.timerId = setInterval(
      () => this.tick(),
      1000
    )
  }

  componentWillUnmount(){
    clearInterval(this.timerId);
  }

  tick() {
    this.setState({
      temperature: Math.round(Math.random() * 100)
    });
  }

  render(){
    return (
    <div className="jumbotron">
    <div className="row justify-content-center">
      <h6 className="display-4">Temperature</h6>
      </div>
      <div className="row justify-content-center margin-top"> 
      <Thermometer
        theme="dark"
        value={this.state.temperature}
        max="100"
        steps="3"
        format="Â°C"
        size="large"
        height="300" 
      /> 
    </div>
    </div> 
  )};
};

export default ThermometerRender;
